import React from 'react';
import './Css/chat.css';
import onlineIcon from '../images/onlineIcon.png';
import closeIcon from '../images/closeIcon.png';

const Infobar = ({room}) => (

   
        <div className='d-flex align-items-center justify-content-between infobar '>
            <div className=' leftContainer d-flex align-items-center ml-4 '>
                <img src={onlineIcon} alt='online Icon' />
                <h3 className='text-white ml-2'>{room}</h3>
            </div>
            <div className='rightContainer d-flex justify-content-end mr-3'>
                    <a href='/'><img src={closeIcon} alt = 'close Icon' /> </a>
            </div>
        </div>

    
);

export default Infobar;